import React from 'react';
import BoardSquare from './BoardSquare';

let squares = [];
for (let i = 1; i < 10; i++) {
  squares.push(<BoardSquare key={i} number={i}/>)
}

let BoardTemplate = () => 
  <div className='board-container'>
    <div className='board'>
      {squares}
    </div>
  </div>

export default BoardTemplate;


